import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { IonicStorageModule } from '@ionic/storage';
import { AngularFireModule } from 'angularfire2';
import { FIREBASE_CONFIG } from './app.firebase.config';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { Facebook } from '@ionic-native/facebook';
import { GooglePlus } from '@ionic-native/google-plus';
import { GlobalProvider } from '../providers/global/global';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, { mode: 'md' }, 
{
  links: [
    { loadChildren: '../pages/alergias/alergias.module#AlergiasPageModule', name: 'AlergiasPage', segment: 'alergias', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cadastro/cadastro.module#CadastroPageModule', name: 'CadastroPage', segment: 'cadastro', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cartao/cartao.module#CartaoPageModule', name: 'CartaoPage', segment: 'cartao', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cirurgias/cirurgias.module#CirurgiasPageModule', name: 'CirurgiasPage', segment: 'cirurgias', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/doencas-cronicas/doencas-cronicas.module#DoencasCronicasPageModule', name: 'DoencasCronicasPage', segment: 'doencas-cronicas', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/historico/historico.module#HistoricoPageModule', name: 'HistoricoPage', segment: 'historico', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#MedicosPageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/info-pessoal/info-pessoal.module#InfoPessoalPageModule', name: 'InfoPessoalPage', segment: 'info-pessoal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/med-uso-continuo/med-uso-continuo.module#MedUsoContinuoPageModule', name: 'MedUsoContinuoPage', segment: 'med-uso-continuo', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/medicos/medicos.module#MedicosPageModule', name: 'MedicosPage', segment: 'medicos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/parentes/parentes.module#ParentesPageModule', name: 'ParentesPage', segment: 'parentes', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/resetar-senha/resetar-senha.module#ResetarSenhaPageModule', name: 'ResetarSenhaPage', segment: 'resetar-senha', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sobre/sobre.module#SobrePageModule', name: 'SobrePage', segment: 'sobre', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot(),
    AngularFireModule.initializeApp(FIREBASE_CONFIG),
    AngularFireAuthModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    Facebook,
    GooglePlus,
    GlobalProvider
  ]
})
export class AppModule { }



